.background {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
    url("../utils/HomePic.jpeg");
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100vh;
  text-shadow: 0 0.05rem 0.1rem rgba(0, 0, 0, 0.5);
  box-shadow: inset 0 0 5rem rgba(0, 0, 0, 0.5);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.warning {
  margin-top:400px;  
  text-transform: uppercase;
  color: rgba(255, 255, 0, 0.705);
  font-size: 30px;
  position: absolute;
}

p {
  padding: 0px;
  margin: 0px;
}

.middleContainer {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: auto;
  margin-top: 200px;
  width: fit-content;
}
.title {
  margin: 0px;
  margin-bottom: 13px;
  font-size: 50px;
  font-weight: 500;
  color: white;
}
.message {
  color: white;
  font-size: 26px;
  line-height: 1.5;
  letter-spacing: -0.5px;
  margin: 0px;
  margin-bottom: 40px;
  text-align: center;
}
.homeButton {
  padding: 15px 19px;
  color: #333;
  background-color: rgba(255, 255, 255, 0.883);
  font-weight: 700;
  border-color: #fff;
  border-radius: 5px;
  border: none;
  font-size: 26px;
  letter-spacing: -0.5px;
  width: unset;
}
.homeButton:hover {
  background-color: white;
  cursor: pointer;
}
.homeFooter {
  margin-top: 250px;
  font-weight: 500;
  justify-content: flex-end;
  color: rgba(255, 255, 255, 0.5) !important;
  font-size: 20.5px;
  letter-spacing: -0.5px;
}

@media only screen and (max-width: 1024px) {
.middleContainer {
  margin-top: 150px;
}
.warning {
  margin-top:260px;  
  font-size: 20px;
}
  .title {
    font-size: 40px;
  }
  .message {
    font-size: 18px;
    margin-bottom: 30px;
  }
  .homeButton {
    font-size: 16px;
  }
  .homeFooter {
    margin-top: 100px;
    font-size: 15px;
  }
}
@media only screen and (max-width: 425px) {
  .warning {
    margin-top:300px;  
    font-size: 15px;
  }
  .title {
    font-size: 30px;
  }
  .message {
    font-size: 18px;
    padding: 20px;
    margin-bottom: 0px;
  }
  .homeButton {
    padding: 10px 14px;
    font-size: 16px;
  }
  .homeFooter {
    margin-top: 130px;
    font-size: 15px;
  }
}